//
// Progress bars
// --------------------------------------------------

// Give the progress background some depth

@progressBarHeightLg: 32px;
@progressBarHeight:   16px;
@progressBarHeightSm: 10px;
@progressBarHeightXs: 2px;

.progress,
.progress-bar {
  height: @progressBarHeight;
  box-shadow: none !important;
}
.progress {
  background-color: @g17-whisper;
  border-radius: (@progressBarHeight /2);

  &.progress-lg {
    height: @progressBarHeightLg;
    border-radius: (@progressBarHeightLg /2);
    .progress-bar {
      height: @progressBarHeightLg;
      
      span {
        line-height: @progressBarHeightLg;
        font-size: 20px;
        font-weight: 400;
      }
    }
  }
  &.progress-sm {
    height: @progressBarHeightSm;
    border-radius: (@progressBarHeightSm /2);
    .progress-bar {
      height: @progressBarHeightSm;
    }
  }
  &.progress-xs {
    height: @progressBarHeightXs;
    border-radius: (@progressBarHeightXs /2);
    .progress-bar {
      height: @progressBarHeightXs;
    }
  }
  // These sizes are too small to have readable text
  &.progress-sm,
  &.progress-xs {
    .progress-bar span {
      display: none !important;
    }
  }
}

.progress-bar {
  background-color: @g15-platinum;
  position: relative;

  span {
    position: absolute;
    font-size: 12px;
    font-weight: 600;
    color: @g8-storm;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: @progressBarHeight;
  }
  // Contextual Alternatives
  &-primary {
    .gradient-h(@c-pool,@c-laser);
    span { color: @g20-white; }
  }
  &-success {
    .gradient-h(@c-rainforest,@c-honeydew);
    span { color: @g20-white; }
  }
  &-info {
    .gradient-h(@g6-smoke,@g8-storm);
    span { color: @g20-white; }
  }
  &-warning {
    .gradient-h(@c-star,@c-comet);
    span { color: @g20-white; }
  }
  &-danger {
    .gradient-h(@c-curacao,@c-dreamsicle);
    span { color: @g20-white; }
  }
  // Seasonal Alternatives
  &-spring {
    .gradient-h(@c-laser,@c-rainforest);
    span { color: @g20-white; }
  }
  &-summer {
    .gradient-h(@c-pool,@c-hydrogen);
    span { color: @g20-white; }
  }
  &-fall {
    .gradient-h(@c-dreamsicle,@c-potassium);
    span { color: @g20-white; }
  }
  &-winter {
    .gradient-h(@c-pool,@c-comet);
    span { color: @g20-white; }
  }
}


.progress-heatmap {
  position: relative;
  background-color: @c-pool;
  background: -moz-linear-gradient(left,  @c-pool 0%, @c-star 57%, @c-curacao 93%, @c-dreamsicle 100%);
  background: -webkit-linear-gradient(left,  @c-pool 0%,@c-star 57%,@c-curacao 93%,@c-dreamsicle 100%);
  background: linear-gradient(to right,  @c-pool 0%,@c-star 57%,@c-curacao 93%,@c-dreamsicle 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='@c-pool', endColorstr='@c-dreamsicle',GradientType=1 );

  .progress-bar {
    float: right;
    background: transparent;
    position: relative;
    background-color: @g17-whisper;
  }
}