//
// Gradient Mixins
// --------------------------------------------------

.gradient-h(@color1,@color2) {
	background: @color1;
	background: -moz-linear-gradient(left,  @color1 0%, @color2 100%);
	background: -webkit-linear-gradient(left,  @color1 0%,@color2 100%);
	background: linear-gradient(to right,  @color1 0%,@color2 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='@color1', endColorstr='@color2',GradientType=1 );
}
.gradient-v(@color1,@color2) {
	background: @color1;
	background: -moz-linear-gradient(top,  @color1 0%, @color2 100%);
	background: -webkit-linear-gradient(top,  @color1 0%,@color2 100%);
	background: linear-gradient(to bottom,  @color1 0%,@color2 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='@color1', endColorstr='@color2',GradientType=0 );
}
.gradient-diag-neg(@color1,@color2) {
	background: @color1;
	background: -moz-linear-gradient(-45deg,  @color1 0%, @color2 100%);
	background: -webkit-linear-gradient(-45deg,  @color1 0%,@color2 100%);
	background: linear-gradient(135deg,  @color1 0%,@color2 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='@color1', endColorstr='@color2',GradientType=1 );
}
.gradient-diag-pos(@color1,@color2) {
	background: @color1;
	background: -moz-linear-gradient(45deg,  @color1 0%, @color2 100%);
	background: -webkit-linear-gradient(45deg,  @color1 0%,@color2 100%);
	background: linear-gradient(45deg,  @color1 0%,@color2 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='@color1', endColorstr='@color2',GradientType=1 );
}
.gradient-r(@color-center,@color-outside) {
	background: @color-center;
	background: -moz-radial-gradient(center, ellipse cover,  @color-center 0%, @color-outside 100%);
	background: -webkit-radial-gradient(center, ellipse cover,  @color-center 0%,@color-outside 100%);
	background: radial-gradient(ellipse at center,  @color-center 0%,@color-outside 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='@color-center', endColorstr='@color-outside',GradientType=1 );
}

