//
// Colored Text Utility
// --------------------------------------------------
span.text-color {
  &-success {
    color: @c-rainforest;
  }
  &-warning {
    color: @c-star;
  }
  &-danger {
    color: @c-curacao;
  }
}

//
// Custom Scrollbar Mixin
// --------------------------------------------------
.custom-scrollbar(@track,@thumb) {
	&::-webkit-scrollbar {
		width: @ix-scrollbar;
		height: @ix-scrollbar;

		&-button {
		  background-color: @track;
		}
		&-track {
		  background-color: @track;
		}
		&-track-piece {
		  background: @track;
		}
		&-thumb {
		  background-color: @thumb;
		  border: (@ix-border * 2) solid @track;
		  border-radius: (@ix-scrollbar / 2);
		}
		&-corner {
		  background-color: @track;
		}
		&-resizer {
		  background-color: @thumb;
		}
	}
}