body,
html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
}
.influx-layout-styles {
  position: absolute;
}
.ix-app,
.ix-sidebar,
.ix-app-wrapper {
  height: 100%;
}
.ix-app {
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.ix-sidebar {
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background: #545667;
  background: -moz-linear-gradient(left, #545667 0%, #2C2C38 100%);
  background: -webkit-linear-gradient(left, #545667 0%, #2C2C38 100%);
  background: linear-gradient(to right, #545667 0%, #2C2C38 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='@color1', endColorstr='@color2', GradientType=1);
}
.ix-app-wrapper {
  z-index: 3;
  position: absolute;
  top: 120px;
  left: 0;
  width: 100%;
  height: calc(100% -  60px );
  overflow: auto;
  background: #F6F6F8;
  background: -moz-linear-gradient(top, #F6F6F8 0%, #FAFAFC 100%);
  background: -webkit-linear-gradient(top, #F6F6F8 0%, #FAFAFC 100%);
  background: linear-gradient(to bottom, #F6F6F8 0%, #FAFAFC 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='@color1', endColorstr='@color2', GradientType=0);
}
.ix-app-wrapper::-webkit-scrollbar {
  width: 14px;
  height: 14px;
}
.ix-app-wrapper::-webkit-scrollbar-button {
  background-color: #FAFAFC;
}
.ix-app-wrapper::-webkit-scrollbar-track {
  background-color: #FAFAFC;
}
.ix-app-wrapper::-webkit-scrollbar-track-piece {
  background: #FAFAFC;
}
.ix-app-wrapper::-webkit-scrollbar-thumb {
  background-color: #22ADF6;
  border: 4px solid #FAFAFC;
  border-radius: 7px;
}
.ix-app-wrapper::-webkit-scrollbar-corner {
  background-color: #FAFAFC;
}
.ix-app-wrapper::-webkit-scrollbar-resizer {
  background-color: #22ADF6;
}
.ix-app-heading {
  z-index: 5;
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  height: 60px;
  background-color: #ffffff;
}
.ix-app-heading:after {
  content: '';
  display: block;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 6px;
  background: rgba(41, 41, 51, 0.02);
  background: -moz-linear-gradient(top, rgba(41, 41, 51, 0.02) 0%, rgba(41, 41, 51, 0) 100%);
  background: -webkit-linear-gradient(top, rgba(41, 41, 51, 0.02) 0%, rgba(41, 41, 51, 0) 100%);
  background: linear-gradient(to bottom, rgba(41, 41, 51, 0.02) 0%, rgba(41, 41, 51, 0) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='@color1', endColorstr='@color2', GradientType=0);
}
.ix-app-heading--container {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}
.ix-app-heading .app-heading-section-styles {
  display: flex;
  align-items: center;
}
.ix-app-heading--left {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  text-align: left;
}
.ix-app-heading--left > * {
  margin: 0 8px 0 0;
}
.ix-app-heading--right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: right;
}
.ix-app-heading--right > * {
  margin: 0 0 0 4px;
}
.ix-app-heading.dark-theme {
  background-color: #0F0E15;
  color: #D4D7DD;
}
.ix-app-title {
  font-size: 21px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.ix-app-splash {
  z-index: 3;
  position: absolute;
  left: 0;
  top: 60px;
  width: 100%;
  height: calc(100% -  60px );
  overflow: auto;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
.ix-app-splash::-webkit-scrollbar {
  width: 14px;
  height: 14px;
}
.ix-app-splash::-webkit-scrollbar-button {
  background-color: #FAFAFC;
}
.ix-app-splash::-webkit-scrollbar-track {
  background-color: #FAFAFC;
}
.ix-app-splash::-webkit-scrollbar-track-piece {
  background: #FAFAFC;
}
.ix-app-splash::-webkit-scrollbar-thumb {
  background-color: #22ADF6;
  border: 4px solid #FAFAFC;
  border-radius: 7px;
}
.ix-app-splash::-webkit-scrollbar-corner {
  background-color: #FAFAFC;
}
.ix-app-splash::-webkit-scrollbar-resizer {
  background-color: #22ADF6;
}
.ix-sidebar .navbar-toggle {
  background-color: transparent;
  width: 60px;
  height: 60px;
  margin: 0 7px 0 0;
  padding: 0;
  border: 0;
}
.ix-sidebar .navbar-toggle .icon-bar {
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: #A4A8B6;
  margin: 0 !important;
  width: 24px;
  height: 2px;
  transform: translate(-50%, -50%);
  transition: top 0.25s ease, opacity 0.25s ease, transform 0.25s ease 0.25s, background-color 0.25s ease;
  opacity: 0;
}
.ix-sidebar .navbar-toggle .icon-bar:nth-of-type(2) {
  opacity: 1;
  transform: translate(-50%, -50%) rotate(45deg);
}
.ix-sidebar .navbar-toggle .icon-bar:nth-of-type(3) {
  opacity: 1;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.ix-sidebar .navbar-toggle.collapsed .icon-bar {
  opacity: 1;
  transform: translate(-50%, -50%);
  transition: top 0.25s ease 0.25s, opacity 0.25s ease, transform 0.25s ease, background-color 0.25s ease;
}
.ix-sidebar .navbar-toggle.collapsed .icon-bar:nth-of-type(2) {
  top: calc(50% - 8px);
  transform: translate(-50%, -50%);
}
.ix-sidebar .navbar-toggle.collapsed .icon-bar:nth-of-type(3) {
  top: calc(50% + 8px);
  transform: translate(-50%, -50%);
}
.ix-sidebar .navbar-toggle:hover {
  background-color: transparent;
}
.ix-sidebar .navbar-toggle:hover .icon-bar {
  background-color: #ffffff;
}
.ix-sidebar .navbar-brand {
  width: 60px;
  height: 60px;
  padding: 0;
  position: relative;
}
.ix-sidebar .navbar-brand span.icon {
  color: #676978;
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 26px;
  transform: translate(-50%, -50%);
}
.ix-sidebar .navbar-brand a:link,
.ix-sidebar .navbar-brand a:active,
.ix-sidebar .navbar-brand a:visited {
  background-color: #EEEFF2;
  color: #676978;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: background-color 0.25s ease;
}
.ix-sidebar .navbar-brand a:hover {
  background-color: #FAFAFC;
  cursor: pointer;
}
.ix-sidebar .navbar-brand a:hover span.icon {
  color: #8E91A1;
}
.ix-sidebar .navbar-collapse {
  position: absolute;
  top: 60px;
  left: 15px;
  width: 100%;
  border: 0;
  background: #22ADF6;
  background: -moz-linear-gradient(left, #22ADF6 0%, #9394FF 100%);
  background: -webkit-linear-gradient(left, #22ADF6 0%, #9394FF 100%);
  background: linear-gradient(to right, #22ADF6 0%, #9394FF 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='@color1', endColorstr='@color2', GradientType=1);
}
.ix-sidebar .navbar-nav {
  display: block;
  margin: 0 -15px;
  width: calc(100% + 30px) !important;
  max-height: 270px;
  overflow: auto;
}
.ix-sidebar .navbar-nav::-webkit-scrollbar {
  width: 14px;
  height: 14px;
}
.ix-sidebar .navbar-nav::-webkit-scrollbar-button {
  background-color: #9394FF;
}
.ix-sidebar .navbar-nav::-webkit-scrollbar-track {
  background-color: #9394FF;
}
.ix-sidebar .navbar-nav::-webkit-scrollbar-track-piece {
  background: #9394FF;
}
.ix-sidebar .navbar-nav::-webkit-scrollbar-thumb {
  background-color: #6BDFFF;
  border: 4px solid #9394FF;
  border-radius: 7px;
}
.ix-sidebar .navbar-nav::-webkit-scrollbar-corner {
  background-color: #9394FF;
}
.ix-sidebar .navbar-nav::-webkit-scrollbar-resizer {
  background-color: #6BDFFF;
}
.ix-sidebar .navbar-nav > li > a:link,
.ix-sidebar .navbar-nav > li > a:active,
.ix-sidebar .navbar-nav > li > a:visited,
.ix-sidebar .navbar-nav > li > a.dropdown-toggle {
  transition: background-color 0.25s ease, color 0.25s ease;
  color: #BEF0FF;
  background-color: transparent;
  position: relative;
  padding-left: 60px;
  font-family: 'Roboto', Helvetica, Arial, Tahoma, Verdana, sans-serif !important;
}
.ix-sidebar .navbar-nav > li > a:link:before,
.ix-sidebar .navbar-nav > li > a:active:before,
.ix-sidebar .navbar-nav > li > a:visited:before,
.ix-sidebar .navbar-nav > li > a.dropdown-toggle:before {
  font-family: 'icomoon' !important;
  display: none;
}
.ix-sidebar .navbar-nav > li > a:hover,
.ix-sidebar .navbar-nav > li > a.dropdown-toggle:hover {
  color: #ffffff;
  background: rgba(69, 145, 237, 0.5);
  background: -moz-linear-gradient(left, rgba(69, 145, 237, 0.5) 0%, #9394FF 100%);
  background: -webkit-linear-gradient(left, rgba(69, 145, 237, 0.5) 0%, #9394FF 100%);
  background: linear-gradient(to right, rgba(69, 145, 237, 0.5) 0%, #9394FF 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='@color1', endColorstr='@color2', GradientType=1);
