  background: -moz-linear-gradient(left, rgba(50, 107, 186, 0.5) 0%, #9394FF 100%);
  background: -webkit-linear-gradient(left, rgba(50, 107, 186, 0.5) 0%, #9394FF 100%);
  background: linear-gradient(to right, rgba(50, 107, 186, 0.5) 0%, #9394FF 100%);
